<template>
  <b-container fluid>
    <PageHeader :header="header" :lead="lead" />

    <ActionRecipients
      :loader="loadRecipients"
      :options="ceremonies"
      :selectAll="selectAllRows"
      :clearSelected="clearSelected"
    />
    <FilterRecipientList :loader="loadRecipients" />

    <b-table
      small
      striped
      id="recipients-table"
      ref="selectableTable"
      :items="items"
      :fields="fields"
      :select-mode="selectMode"
      responsive="sm"
      :selectable="isAdmin"
      @row-selected="onRowSelected"
      selected-variant="info"
      :busy.sync="loading"
    >
      <template #cell(is_declared)="row">
        <b-badge class="m-1" v-if="!!row.item.is_declared" variant="success">Registered</b-badge>
        <b-badge class="m-1" v-else variant="warning">Draft</b-badge>
        <b-badge class="m-1" v-if="!!row.item.historical" variant="primary">Historical</b-badge>
        <b-badge class="m-1" v-if="!!row.item.retiring_this_year" variant="info">Retiring</b-badge>
      </template>

      <template #cell(organization)="row">
        {{ lookupOrganization(row.item.organization) }}
      </template>

      <template #cell(ceremony_opt_out)="row">
        <b-badge
          class="m-1"
          v-for="(attendance, index) in row.item.attendee"
          v-bind:key="index"
          :variant="getStatus(attendance.status)"
        >
          {{ lookup('assignments', attendance.status) }}: {{ new Date(attendance.ceremonies.scheduled_datetime).toLocaleString() }}
        </b-badge>
        <b-badge class="m-1" v-if="row.item.ceremony_opt_out" variant="danger">Opt-Out</b-badge>
      </template>

      <template #cell(created)="row">
        {{ row.item.created.toLocaleString() }}
      </template>

      <template #cell(updated)="row">
        {{ row.item.updated.toLocaleString() }}
      </template>

      <template #cell(details)="row">
        <b-button-group>
          <b-button
            size="sm"
            class="m-1"
            @click="reroute(`/recipients/view/${row.item.id}`)"
          >
            View
          </b-button>
          <b-button
            size="sm"
            class="m-1"
            @click="reroute(`/recipients/edit/${row.item.id}`)"
          >
            Edit
          </b-button>
          <b-button
            size="sm"
            class="m-1"
            @click="reroute(`/recipients/delete/${row.item.id}`)"
          >
            Delete
          </b-button>
        </b-button-group>
      </template>

    </b-table>

    <b-alert v-if="error" show variant="danger">
      A server error occurred. Please contact the site administrator for assistance.
    </b-alert>

  </b-container>
</template>

<script>

import FilterRecipientList from "../../components/FilterRecipientList"
import PageHeader from '@/components/PageHeader'
import ActionRecipients from '@/components/ActionRecipients'
import {getRecipients} from "@/services/api.services"
import optionsServices from '@/services/options.services'

export default {
  name: 'recipients-list',
  props: ['header', 'lead', 'mode'],
  components: {
    PageHeader,
    ActionRecipients,
    FilterRecipientList
  },
  data () {
    return {
      loading: false,
      processing: false,
      processingAction: false,
      error: false,
      message: '',
      current_page: 0,
      first_page_url: '',
      last_page_url: '',
      last_page: '',
      links: [],
      next_page_url: '',
      path: '',
      per_page: 0,
      prev_page_url: '',
      from: 0,
      to: 0,
      total: 0,
      modes: ['multi', 'single', 'range'],
      selectMode: 'multi',
      items: [],
      fields: [
        {key: 'is_declared', label: 'Status'},
        {key: 'first_name'},
        {key: 'last_name'},
        {key: 'employee_number', label: 'Employee No.'},
        {key: 'organization'},
        {key: 'branch_name', label: 'Branch'},
        {key: 'milestone'},
        {key: 'ceremony_opt_out', label: 'Ceremony'},
        {key: 'updated'},
        {key: 'created'},
        {key: 'details', label: ''}
      ]
    }
  },
  computed: {
    isLoading () {
      return this.$store.getters.isLoading
    },
    isOrgContact () {
      return this.$store.getters.isOrgContact
    },
    isAdmin () {
      return this.$store.getters.isAdmin
    },
    isSuperAdmin () {
      return this.$store.getters.isSuperAdmin
    },
    user() {
      return this.$store.getters.getUser
    },
    ceremonies() {
      return this.$store.getters.getCeremonies
    },
    organizations() {
      // filter organizations by recipient authorization
      const orgs = this.$store.getters.getOrganizations
      return this.user.role === 'orgContact'
        ? orgs.filter(org => {
          return (this.user.organizations || []).includes(org.value) || !org.value
        })
        : orgs
    },
  },
  methods: {
    onRowSelected(items) {
      this.$store.commit('setSelected', items)
    },
    selectAllRows() {
      this.$refs.selectableTable.selectAllRows()
    },
    clearSelected() {
      this.$refs.selectableTable.clearSelected()
    },
    async loadRecipients (pageNum, filters=[]) {
      this.processing = true
      this.error = false
      const [error, recipients] = await getRecipients(pageNum, filters)
      if (error) {
        this.error = true
        this.$store.commit('setMessage', error)
      }
      // get data and pagination links
      const {
        data=[],
        current_page=0,
        first_page_url='',
        last_page_url='',
        last_page = '',
        links = [],
        next_page_url = '',
        path = '',
        per_page = 0,
        prev_page_url = '',
        from = 0,
        to = 0,
        total = 0,
      } = recipients || {}

      // set pagination
      this.$store.commit('setPagination', {
        current_page: current_page,
        first_page_url: first_page_url,
        last_page_url: last_page_url,
        last_page: last_page,
        links: links,
        next_page_url: next_page_url,
        path: path,
        per_page: per_page,
        prev_page_url: prev_page_url,
        from: from,
        to: to,
        total: total,
      })

      // filter draft for non super-admin users
      this.items = (data || [])
        .filter(recipient => {
          return !!recipient.is_declared || this.isSuperAdmin || this.isAdmin
        })
        .map(recipient => {
          const {
            id=null,
            first_name=false,
            last_name=null,
            employee_number=null,
            milestones=null,
            historical=false,
            retiring_this_year=0,
            ceremony_opt_out=0,
            organization_id='',
            branch_name='',
            is_declared=0,
            attendee=null,
            scheduled_datetime = null,
            updated_at=null,
            created_at=null,
          } = recipient || {}
          const updatedTS = new Date(updated_at)
          const createdTS = new Date(created_at)
          return {
            id: id,
            is_declared: is_declared,
            attendee: attendee,
            scheduled_datetime: scheduled_datetime ? new Date(scheduled_datetime).toLocaleString() : null,
            first_name: first_name,
            last_name: last_name,
            employee_number: employee_number,
            organization: organization_id,
            historical: historical,
            milestone: milestones,
            branch_name: branch_name,
            retiring_this_year: retiring_this_year,
            ceremony_opt_out: ceremony_opt_out,
            created: createdTS,
            updated: updatedTS,
          }
        })
      this.processing = false
    },
    async reroute(uri) {
      await this.$router.push(uri)
    },
    lookup(key, value) {
      return optionsServices.lookup(key, value)
    },
    lookupOrganization(value) {
      const org = this.organizations.filter(org => {
        return String(org.value) === String(value)
      })
      if (org.length > 0) {
        const {text = ''} = org[0] || {}
        // return organization shortname
        return text ? text.substring(0, text.lastIndexOf("-")) : '';
      }
      return value
    },
    getStatus(status) {
      const variants = {
        assigned: 'info',
        waitlisted: 'warning',
        invited: 'dark',
        attending: 'success',
        declined: 'danger'
      }
      return variants[status] || 'secondary'
    },
  },
  async beforeMount () {
    this.$store.commit('setLoading', true)
    await this.loadRecipients()
    this.$store.commit('setLoading', false)
  }
}
</script>
