<template>
  <b-container fluid>
    <PageHeader :header="this.header" :lead="this.lead" />

    <b-container v-if="!isLoading">
      <b-alert v-if="!isAdmin" variant="info" show>Sign in to view this page.</b-alert>
      <b-row align-h="between">
        <b-col cols="6">
          <b-form-group
            label="Category"
            label-for="table-select-filter-category"
            label-cols="3"
          >
            <b-form-select
              id="table-select-filter-category"
              v-model="selectCategory"
              :options="categories || []"
              class="mb-3"
            ></b-form-select>
          </b-form-group>
        </b-col>
        <b-col>
          <b-button size="sm" @click="clearFilters">Clear Filters</b-button>
        </b-col>
      </b-row>
    </b-container>

    <b-table
      :items="attendees || []"
      :fields="fields"
      striped
      :filter="criteria"
      :filter-function="applyFilter"
      :sort-by="sortBy"
      :sort-desc="sortDesc"
      responsive="sm"
      :busy.sync="loading"
    >

      <template #cell(status)="row">
        <b-badge
          class="m-1"
          v-for="(attendance, index) in row.item.attendee"
          v-bind:key="index"
          :variant="getStatus(attendance.status)"
        >
          {{ lookup('assignments', attendance.status) }}: {{ new Date(attendance.ceremonies.scheduled_datetime).toLocaleString() }}
        </b-badge>
        <b-badge class="m-1" v-if="row.item.ceremony_opt_out" variant="danger">Opt-Out</b-badge>
      </template>

      <template #cell(details)="row">
        <b-button
          v-if="row.item.format !== 'csv'"
          size="sm"
          class="m-1"
          @click="reroute(`/reports/view/${row.item.id}`)"
        >
          View
        </b-button>
      </template>

    </b-table>

    <b-alert v-if="error" show variant="danger">
      A server error occurred. Please contact the site administrator for assistance.
    </b-alert>

  </b-container>
</template>

<script>

import PageHeader from '@/components/PageHeader'
import reportServices from "@/services/reports.services";
import {get} from "@/services/api.services";

export default {
  name: 'reports-list',
  props: ['header', 'lead', 'mode'],
  components: {
    PageHeader
  },
  data () {
    return {
      loading: false,
      error: false,
      message: '',
      sortBy: 'name',
      sortDesc: true,
      selectCategory: '',
      fields: [
        {key: 'status', label: 'Status', sortable: true},
        {key: 'first_name', label: 'First Name', sortable: true},
        {key: 'last_name', label: 'Last Name', sortable: true},
        {key: 'details', label: '', sortable: false}
      ],
      attendees: []
    }
  },
  computed: {
    isLoading () {
      return this.$store.getters.isLoading
    },
    isOrgContact () {
      return this.$store.getters.isOrgContact
    },
    isAdmin () {
      return this.$store.getters.isAdmin
    },
    isSuperAdmin () {
      return this.$store.getters.isSuperAdmin
    },
    reports () {
      return reportServices.reports() || []
    },
    categories () {
      return reportServices.categories()
    },
    criteria () {
      return {
        category: this.selectCategory
      }
    },
  },
  methods: {
    clearFilters() {
      this.selectCategory = ''
    },
    applyFilter(row) {
      return !this.criteria.category || row.category === this.criteria.category
    },
    async reroute(uri) {
      await this.$router.push(uri)
    },
    async loadData (id) {
      this.processing = true
      this.error = false
      const [error, data] = await get(`attendees/list/${id}`)
      if (error) {
        this.error = true
        this.$store.commit('setMessage', error)
      }
      this.attendees = data

      console.log(this.attendees)
      this.processing = false
    },
    getStatus(status) {
      const variants = {
        assigned: 'info',
        waitlisted: 'warning',
        invited: 'dark',
        attending: 'success',
        declined: 'danger'
      }
      return variants[status] || 'secondary'
    },
  },
  async beforeMount () {
    this.$store.commit('setLoading', true)
    // retrieve attendees for this ceremony
    await this.loadData(this.$route.params.id || '')
    this.$store.commit('setLoading', false)
  }
}
</script>
